/* .features-gap{padding: 2% 5%;} */
.pagetitle{font-size: 3rem;  text-align: center; color: #212d39;}
.pagetitle span{color: #0a6ffb;}
.torychat{max-width: 60%;
  text-align: center;
  font-size: 1.2rem;
  margin: 1% auto;}

.torychat-pic img{margin-top: -6rem;}
.connect-title{text-align: center; margin: 0 auto;}
.feature-font-3rem{font-size: 3rem;}
.feature-font-1rem{font-size: 1.3rem;}
.connect-text{ text-align: center; margin: 15px auto;}
.text-center{text-align: center;}
.connect button a{color: #fff;}
.connect .ui.button, .ui.button{border-radius: 10px;}
.button-bg{background-size: 100px;
  background-position: top right;
  background-repeat: no-repeat;
  margin-top: 3rem;
}
.gift-card .vector-part img{ margin: 0 auto;}
.onboard img{margin: 0 auto;}

.hereto-help{max-width: 150px; text-decoration: underline;}
.feature-font-4rem{font-size: 4rem;}

.ui.grid>.row>.column.section-flex{display: flex;align-items: center;}
/* .easy-image img{float: right;} */
.easy-out{position: relative;
  display: block;
  width: 100%;
  float: left;}
.font-w7{font-weight: 700;}
.easy-text{position: absolute;
  top: 0%;
  left: 0;
  width: 50%;}
.wehelp{padding-top: 5%;max-width: 260px; font-size: 2.7rem;line-height: normal;}
.connect, .easy-deals-feature-2{background-repeat: no-repeat;
  background-position: top right;
  margin-top: -100px;
  background-size: 80px;
  padding-top: 140px;}

.easy-deals-feature{background-size: 80px;
  background-position: top right;
  margin-top: -140px;
  padding-top: 140px;
  background-repeat: no-repeat;
  padding-bottom: 0 !important;
}
.feature-last-bg{display: flex;}
.feature-last-bg img{margin-left: 36%; margin-top: -7%;}
.easy-image{display: flex;justify-content:flex-end;}
.orepass{color: #b1b1b1;}
.footer-outer{margin: 0 !important;}
.text-middle{display: flex !important; align-items: center;}

.features{margin-top: 2rem;}













@media only screen and (min-width: 1300px) {
  .connect .ui.container, 
  .onboard .ui.container, 
  .easy-deals-feature .ui.container{width: 1250px !important;}
  /* .ui.container{width: 1250px !important;} */
    }

@media only screen and (min-width: 1199px) {

.torychat-pic img{margin: 0 auto;}
  }
@media only screen and (max-width: 1199px) {
  .features .ui.grid{margin-left: 0;  margin-right: 0;}
  .connect-title{max-width: 80%;}

  .torychat{max-width: 80%;}
  .torychat-pic img{margin-top: 0; margin: 0 auto;}
  .connect-text{max-width: 60%;}
  .feature-last-bg img{margin-left: 26%; margin-top: -5%;}

}

@media only screen and (max-width: 991px) {
  .torychat{max-width: 100%;}
  .torychat-inner{text-align: justify;}
  .connect-text, .connect-title{max-width: 100%;}
  .feature-font-3rem{font-size: 2.5rem;}
  .feature-font-4rem{font-size: 3rem;}
  .easy-deals-feature{margin-top: 0px; padding-top: 0px; }
  .feature-last-bg img{margin-left: 12%; margin-top: -5%; max-width: 200px !important;}
  .wehelp{margin: 30px 0 0 0; padding-top: 0; max-width: 60%; font-size: 2rem;}
  .easy-deals-feature{background-image: none !important; padding-top: 30px;}
  .easy-deals-feature .ui.container{width: 98% !important;}
  .features{padding: 0 2rem;}
}

@media only screen and (max-width: 820px) {
.easy-text{position: relative; top: 0; width: 100%;text-align: center;}
.easy-deals-feature-2{padding-top: 0;}
.wehelp{ max-width: 100%;}
}

@media only screen and (max-width: 767px) {
 
  .torychat{max-width: 100%;}
  .feature-last-bg img{margin-left: 6%; margin-top: -5%; max-width: 150px !important;}
  .nomobile{display: none;}
  .connect{margin-top: 0; padding-top: 30px; padding-bottom: 30px; background-image: none !important;}
  .easy-deals-feature .ui.container{width: auto!important;}
}
@media only screen and (max-width: 690px) {
.wehelp{margin: 15px auto 0 auto; padding-top: 0; width: 100%; font-size: 1.5rem;}
}

@media only screen and (max-width: 620px) {
.torychat{max-width: 100%;}
.pagetitle{font-size: 2rem;}
.connect .ui.grid>.row{flex-direction: column-reverse; display: flex;}
.text-middle{justify-content: center;}
.ui.grid>.row>.column.full-width,
.ui.grid>.row>.column.section-flex{width: 100% !important; text-align: center;}
.ui.grid>.row>.column.mobile-full{width: 100% !important; text-align: center;}
.hereto-help{max-width: 100%;}
.feature-font-4rem{font-size: 2rem;}
.wehelp{padding: 15px 0 0 0; text-align: center; margin: 0 auto;}
.talkfreely{text-align: justify;}
.feature-font-3rem{font-size: 1.8rem;}
.easy-deals-feature .row{padding-bottom: 0;}
}

@media only screen and (max-width: 520px) {
  .features .pagetitle{margin-top: 30px;}
  .connect-text{text-align: justify;}
  .feature-last-bg img{margin-left: 10%; margin-top: -5%; }
}































